/*----------------------------------------
Transforms */
.lt_dmb-banner {
  background: linear-gradient(#365B9B 40%, transparent 90%);
  padding: 4.5rem 0 4rem;
}
@media (min-width: 992px) {
  .lt_dmb-banner {
    padding: 6rem 0 5.5rem;
  }
}

.lt_dmb-h1,
.lt_dmb-h2 {
  line-height: 1;
}
.lt_dmb-h1 span,
.lt_dmb-h2 span {
  display: block;
}

.lt_dmb-h1 {
  font-size: 5rem;
}
.lt_dmb-h1 .lt_dmb-h-small {
  font-size: 3rem;
}

.lt_dmb-h2 {
  font-size: 3.25rem;
}
.lt_dmb-h2 .lt_dmb-h-small {
  font-size: 1.875rem;
}
@media (min-width: 992px) {
  .lt_dmb-h2 {
    font-size: 5rem;
  }
  .lt_dmb-h2 .lt_dmb-h-small {
    font-size: 3rem;
  }
}

.lt_dmb-app-logo {
  margin-top: 13.5vw;
}
@media (min-width: 768px) {
  .lt_dmb-app-logo {
    margin-top: 4.5rem;
  }
}
@media (min-width: 992px) {
  .lt_dmb-app-logo {
    margin: 4.5rem 0;
    width: 108px;
    height: 108px;
  }
}

.lt_dmb-offleft,
.lt_dmb-offright {
  position: relative;
}

.lt_dmb-offleft {
  left: -1.875rem;
}

.lt_dmb-offright {
  left: 1.875rem;
}

.lt_dmb-img-b {
  border: 0.25rem solid #151515;
  border-right: 0;
}
@media (min-width: 768px) {
  .lt_dmb-img-b {
    border-width: 0.5rem;
  }
}
@media (min-width: 992px) {
  .lt_dmb-img-b {
    border-width: 1rem;
  }
}

.lt_dmb-left-col {
  padding-top: 2vw;
}
@media (min-width: 992px) {
  .lt_dmb-left-col {
    padding-top: 2rem;
  }
}

.lt_dmb-center-col {
  padding-top: 13.5vw;
  padding-bottom: 3rem;
}
@media (min-width: 768px) {
  .lt_dmb-center-col {
    padding-top: 4.5rem;
  }
}
@media (min-width: 992px) {
  .lt_dmb-center-col {
    padding-top: 1rem;
    padding-bottom: 6rem;
  }
}

.lt_dmb-dark-banner-bg {
  background-color: #1C1C41;
  background-image: radial-gradient(circle at 100% 100%, #373C74, transparent 60%), radial-gradient(circle at 85% 15%, #0B0B17, #1C1C41 60%);
}